@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #E3B115;
}


.home_page {
	position: relative;
	height: 100vh;
	overflow-x: hidden;
}

#background_logo {
	position: absolute;
	top: 50vh;
	left: 50vw;
	transform: translate(-50%, -50%);
	z-index: -5;
	height: 80vh;
}

.main_photo {
	position: absolute;
	top: 0;
	right: -1000px;
	height: 100vh;
	z-index: -1;
	transition: right 2s ease-out;
}

.main_photo.show {
	right: 0;
}

#name {
	font-family: 'Montserrat', sans-serif;
	font-weight: bold;
	color: white;
	position: absolute;
	font-size: 96px;
	letter-spacing: .25em;
	top: calc(20vh);
	z-index: 10;
	transition: left 2s ease-out;
}

#lastname {
	font-family: 'Montserrat', sans-serif;
	font-weight: 300;
	color: white;
	position: absolute;
	font-size: 96px;
	letter-spacing: .1em;
	top: calc(20vh + 120px);
	z-index: 10;
	transition: left 2s ease-out;
}

#mini_description {
	font-family: 'Montserrat', sans-serif;
	font-weight: 300;
	color: white;
	position: absolute;
	font-size: 48px;
	top: calc(20vh + 300px);
	z-index: 10;
	transition: left 2s ease-out;
}

.hide_desc {
	left: -1000px;
}

.show_desc {
	left: 215px;
}

.nav_bar {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	padding: 0 50px;
	padding-top: 25px;
}

.nav_bar > #logo {
	margin-right: auto;
}

.nav_bar > #logo:hover {
	cursor: pointer;
}

.nav_bar > #link {
	color: white;
	font-family: 'Montserrat', sans-serif;
	text-decoration: none;
	padding: 0 2%;
	font-size: 18px;
}

.social_media {
	position: absolute;
	margin: 20px 50px;
	bottom: 0;
	z-index: 10;
}

.social_media > img {
	padding: 10px;
}

